import React, { Fragment, useState } from "react";
import { Row, Col } from "reactstrap";
import "./ProjectInfo.css";
import queryString from "query-string";
import projects from "../../helpers/projects";
import technology from "../../helpers/technology";
import { useLocation } from "react-router-dom";

const ProjectInfo = (props) => {
  const location = useLocation();
  const params = queryString.parse(location.search);
  console.log(params.name);

  const data = projects.find((e) => {
    return e.name === params.name;
  });

  useState(()=>{
    window.scrollTo({ top: 0, behavior: 'smooth' })
  },[])

  console.log(data);
  const [imagefile, setimagefile] = useState(technology);
  const [open, setopen] = useState(false);
  const [mouseOver, setmouseOver] = useState(false);
  const [Gallary, setGallary] = useState(false);
  const [photos, setphotos] = useState("");
  const info = [
    {
      id: 1,
      name: "Island",
      image:
        "https://images.unsplash.com/photo-1442530792250-81629236fe54?ixlib=rb-0.3.5&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=980&h=980&fit=crop&s=9631adb2d2f752e3a0734f393fef634b",
    },
    {
      id: 2,
      name: "Forest",
      image:
        "https://images.unsplash.com/photo-1468851508491-4f854ec88aa0?ixlib=rb-0.3.5&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=900&h=900&fit=crop&s=b1222b6a1d3694cac76d2a23c3a02254",
    },
    {
      id: 3,
      name: "Whale",
      image:
        "https://images.unsplash.com/photo-1454991727061-be514eae86f7?ixlib=rb-0.3.5&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=900&h=900&fit=crop&s=3c55430f01fe9ac9a9ccb3383d1416ff",
    },
    {
      id: 4,
      name: "Mountain",
      image:
        "https://images.unsplash.com/photo-1467890947394-8171244e5410?ixlib=rb-0.3.5&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=980&h=980&fit=crop&s=9396f0adf263b51b44626228225684d0",
    },
    {
      id: 5,
      name: "Boat",
      image:
        "https://images.unsplash.com/photo-1443302382600-0bfacc473376?ixlib=rb-0.3.5&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=980&h=980&fit=crop&s=0c0f26518c1001f67b6c2e4480a8d3e0",
    },
    {
      id: 6,
      name: "Flowers",
      image:
        "https://images.unsplash.com/photo-1429091443922-e7d9ae79a837?ixlib=rb-0.3.5&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=900&h=900&fit=crop&s=e81cb6a60c53788559edb9bec21b80fc",
    },
    {
      id: 7,
      name: "Fire",
      image:
        "https://images.unsplash.com/photo-1468245856972-a0333f3f8293?ixlib=rb-0.3.5&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=900&h=900&fit=crop&s=1f57cc13084e32839627453821a43abf",
    },
    {
      id: 8,
      name: "Garden",
      image:
        "https://images.unsplash.com/photo-1427392797425-39090deb14ec?ixlib=rb-0.3.5&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=900&h=900&fit=crop&s=8bfe49466d0da200e61128a8ab0e8fbe",
    },
    {
      id: 9,
      name: "Bridge",
      image:
        "https://images.unsplash.com/photo-1445723356089-6dbb51d9c4f8?ixlib=rb-0.3.5&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=980&h=980&fit=crop&s=6e476c6e7ce1adac161295616d1bec05",
    },
  ];

  function _mouseEnter(e) {
    e.preventDefault();
    if (mouseOver === false) {
      //  console.log(this.props.data.name);
      setmouseOver(true);
    }
  }

  function _mouseLeave(e) {
    e.preventDefault();
    if (mouseOver === true) {
      setmouseOver(false);
    }
  }

  function _clickHandler(y) {
    setopen(true);
    setGallary(true);
    setphotos(y);
  }

  return (
    <Fragment>
      <div className="project-info">
        <div className="project-intro text-center py-5">
          <h2>{data.name}</h2>
        </div>

        <div className="project-details py-3 px-3 mx-auto mt-4">
          <Row>
            <Col md="3">
              <span>
                <img src={data.img} height="200" width="200"></img>
              </span>
            </Col>
            <Col md="9">
              <div>
                <h3 className="font-weight-bold">{data.name}</h3>
              </div>
              <div className="d-flex mt-4">
                <h5>
                  <strong>Project Overview</strong>
                </h5>
              </div>
              {data.description}
              <div className="d-flex mt-4">
                <h5>
                  <strong>Website:</strong>
                </h5>
                <a className="ml-2" href={data.url}>
                  {data.url}
                </a>
              </div>
              <div className="project-techno d-flex align-items-baseline">
                <h5 className="mr-2">
                  <strong>Technology:</strong>
                </h5>
                <span>
                  {data.type.map((p, key) => {
                    const imge = imagefile.find((x) => x.name == p) ? (
                      imagefile.find((x) => x.name == p).img
                    ) : (
                      <span></span>
                    );
                    return imge;
                  })}
                </span>
              </div>
            </Col>
          </Row>
        </div>
        <div className="py-2 ml-4 px-4 projectOver">
          <h3>
            <strong>Gallary</strong>
          </h3>
          {Gallary == false && (
            <div className="rowshots">
              {data.images.map((y) => {
                return (
                  <div className="shots">
                    <img
                      onMouseEnter={_mouseEnter}
                      onMouseLeave={_mouseLeave}
                      onClick={() => _clickHandler(y)}
                      src={y}
                      alt={y.name}
                    />
                  </div>
                );
              })}
            </div>
          )}
        </div>

        {/* {data.url && <div class="wrapper">
          <div class="devices">
            <div class="desktop">
              <div class="desktop-iframe-container">
                <iframe
                  frameborder="0"
                  src={data.url}
                  id="iframe-desktop"
                  target="_blank"
                  allowfullscreen
                ></iframe>
              </div>
            </div>
          </div>
        </div>} */}
      </div>
    </Fragment>
  );
};

export default ProjectInfo;
